.button {
  color: #d41443;
  background-color: #fff;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  padding: 12px 18px;
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
