  width: 40px;
  height: 40px;
  border-radius: 50%;
  padding: 0;
}

.button__icon {
  stroke: currentColor;
  margin-left: 10px;
}

.button--circle .button__icon {
  fill: currentColor;
  margin-left: 0;
}

.header {
